<i18n src="../../../../locales/index.json"></i18n>

<template>
    <div class="row no-gutters">
      <div  class="col-sm-12">
        <div class="card-body">
          <h5 class="card-title">{{name.en}} </h5>
          <p v-if="cleanUrl" class="card-text"><a :href="cleanUrl" target="_blank" rel="noopener noreferrer">{{url}}</a></p>
          <p class="card-text">
            <span class="badge badge-pill badge-secondary">{{getCountry.name}}</span>
          </p>

          <p class="card-text" v-if="getOrgTypes.length">
            <span class="badge badge-pill badge-dark mx-1" v-for="(t,index) in getOrgTypes"  v-bind:key="index"> {{t.name}} </span>
          </p>
        </div>
      </div>
    </div>
</template>

<script>
import { lookUp } from '@scbd/cached-apis'
import { get$http } from '@scbd/load-http'

export default {
  name : 'Organization',
  props: {
    name   : { type: Object, required: true },
    types  : { type: Array, required: true },
    image  : { type: Object },
    url    : { type: String, required: false },
    country: { type: Object, required: true }
  },
  methods: { trimHyperTextProtocol },
  data,
  mounted,
  created
}

function data(){ return { getOrgTypes: [], getCountry: {}, cleanUrl: '' } }

async function created(){
  this.cleanUrl = await addProtocol(this.url)
}
async function  mounted(){
  this.getCountry  = await lookUp('countries', this.country, true)
  this.getOrgTypes = await lookUp('orgTypes', this.types)
}

async function testUri (url){
  const $http   = await get$http()
  const headers = { Authorization: 'Bearer svOIo5CkADUxtddDIQsmSufMxrntG6WtIGDAQEuGMV1HGKM1RS1F5BmPspcL0YSK' }
  const { data } = await $http.post('https://injo3jyxlb.execute-api.us-east-1.amazonaws.com/stg/test', { json: { body: { url } }, headers })

  return data
}

function trimHyperTextProtocol (urlStr){
  try{
    const { host, pathname, search } = new URL(urlStr)
    const cleanPath                  = pathname.length===1? '' : pathname

    return `${host}${cleanPath}${search}`
  }
  catch (ex){ return urlStr }
}

async function addProtocol(urlStr){
  if(!urlStr) return ''
  const url   = trimHyperTextProtocol(urlStr)
  const https = `https://${url}`
  const http  = `http://${url}`

  if(await testUri(https)) return https
  if(await testUri(http)) return http

  return ''
}


</script>

<style scoped>
  .entity-badge{ position: absolute; top: 0; left: 0; border-top-left-radius: 0; }
</style>
